$borderColor:black;

.loginForm{
    border: 2px solid $borderColor;
    border-radius: 5px;
    padding: 10px;
    margin-top: 15px;
    width:40%;
    height:120%;
    background-color: darkslategray;
    color: ivory;
    font-weight: bold;
    box-shadow: 2px 2px gray;
    margin:auto;
    font-variant: small-caps;

}
.Logintitle{
    font-variant: small-caps;
    font-style: italic;
    
    
}
.navigateBar{
    border-radius: 5px;
    padding:15px;
    margin-bottom: 25px;
    border: 2px outset darkslategray;
   
   
}
.appTitle{
    text-align: right;
    color:whitesmoke;
    margin: auto;
    font-variant: small-caps;
    background-color: black;
    border:2px outset slategray;
    padding: 5px;
    border-radius:5px;
    }